import { get, set } from "./store";
import { clientId } from "../config";

export let client;

export function checkAuthStatus() {
  // This is set on the window in `index.html`.
  if (Object.keys(get("token")).length) {
    return get("expireAt") > Date.now() ? false : true;
  } else {
    return false;
  }
}

