export function signIn() {
  client.requestAccessToken();
}

export async function signOut(handleSignInChange) {
  localStorage.removeItem("token");
  handleSignInChange(false);
  google.accounts.oauth2.revoke(get("token"), (done) => {
    if (done.error) {
    } else {
    }
  });
}

export function signInCallback(handleSignInChange) {
  client = google.accounts.oauth2.initTokenClient({
    client_id: clientId,
    scope: "https://www.googleapis.com/auth/analytics.readonly",
    callback: (response) => {
      if (response.error) {
        console.log(response.error);
      } else {
        set("token", response.access_token);
        set("expireAt", Date.now() + Number(response.expires_in));
        handleSignInChange(true);
      }
    },
  });
}
